.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	.scroll-icon {
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translate(-50%, 120%);
	}
	.ellipse {
		position: absolute;
		top: 4rem;
		left: 50%;
		width: 50%;
		height: calc(100% - 4rem);
		transform: translateX(-50%);
		background-color: rgba($color: #fff, $alpha: 0.8);
		border-radius: 500px;
		@include max-lg {
			width: 60%;
		}
		@include max-md {
			width: 90%;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 320px;
		transform: translateY(-90%);
		@include max-lg {
			width: 280px;
		}
		@include max-md {
			transform: translate(-20%, -90%);
			width: 140px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: -5%;
		width: 320px;
		transform: translateY(-90%);
		@include max-lg {
			width: 280px;
		}
		@include max-md {
			width: 140px;
		}
	}
	.orn-3 {
		position: absolute;
		left: 50%;
		top: 0;
		transform: translate(-50%, -100%);
		width: 380px;
		@include max-md {
			width: 230px;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		@include max-md {
			.image-element {
				img {
					height: 200px;
					object-fit: cover;
				}
			}
		}
	}
	.orn-5 {
		position: absolute;
		top: 0;
		left: 0;
		width: 340px;
		transform: translateY(-10%);
		@include max-lg {
			transform: translate(-30%, -40%);
		}
		@include max-md {
			width: 200px;
		}
	}
	.orn-6 {
		position: absolute;
		top: 0;
		right: 8rem;
		width: 180px;
		transform: translateY(-60%) scaleX(-1);
		@include max-md {
			right: 0;
			width: 140px;
		}
	}
	.orn-7 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-20%, -30%);
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
		}
		.orn-13 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 160px;
			transform: translate(0, 90%);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
		}
	}
	.orn-8 {
		position: absolute;
		top: 0;
		right: 0;
		width: 180px;
		transform: translate(20%, -30%) rotate(-65deg);
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
		}
		.orn-13 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 160px;
			transform: translate(-65%, 25%) rotate(-45deg);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 240px;
		transform: translate(-30%, 10%);
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 140px;
		}
	}
	.orn-10 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 240px;
		transform: translate(10%, 10%) scaleX(-1);
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			transform: translate(35%, 10%) scaleX(-1);
			width: 140px;
		}
		@include max-xs {
			width: 140px;
		}
	}
	.orn-11 {
		position: absolute;
		bottom: 0;
		left: 2rem;
		width: 180px;
		transform: translateY(50%);
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
		}
		@include max-xs {
			width: 100px;
		}
		.orn-14 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(60%, 0%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 100px;
			}
		}
	}
	.orn-12 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 180px;
		transform: translate(-60%, 50%) scaleX(-1);
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			transform: translate(-40%, 50%) scaleX(-1);
			width: 120px;
		}
		@include max-xs {
			width: 100px;
		}
		.orn-14 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 180px;
			transform: translate(40%, 20%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 100px;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 6/4;
		margin: 0 auto;
		margin-top: 3rem;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			padding: 8px;
			.background-header {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: var(--border-radius);
			}
		}
	}
	.header-content {
		position: relative;
		.header {
			position: relative;
			display: block;
			width: 50%;
			margin: 0 auto;
			padding: 4.5rem 0;
			@include max-lg {
				width: 80%;
			}
			@include max-md {
				width: 100%;
				padding: 4rem 0;
			}
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
					padding: 3rem 0 0;
					border-radius: var(--border-radius);
					@include max-lg {
						padding: 4rem 0 0;
					}
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}
